body {
    font-family: Arial, sans-serif;
    background-color: #f4f4f9;
    margin: 0;
    padding: 0;
  }
  
  .App {
    text-align: center;
  }
  
  nav {
    background-color: #003366;
    padding: 1rem;
    color: white;
    display: flex;
    justify-content: space-between;  /* Distribute space between the h1 and the ul */
    align-items: center;
  }
  
  nav h1 {
    margin: 0;
  }
  
  nav ul {
    list-style: none;
    padding: 0;
    
  }
  
  nav ul li {
    display: inline;
    margin: 0 1rem;
  }
  
  nav ul li a {
    color: white;
    text-decoration: none;
  }
  
  nav ul li a:hover {
    text-decoration: underline;
  }
  
  h2 {
    color: #333;
  }
  
  p {
    color: #666;
  }
  
  div {
    margin: 2rem 0;
  }
  
  ul {
    list-style: none;
    padding: 0;
  }
  
  ul li {
    margin: 0.5rem 0;
  }
  
  ul li a {
    color: #003366;
    text-decoration: none;
  }
  
  ul li a:hover {
    
    color: rgb(243, 211, 31);
  }
  
  #post1, #post2, #post3, #post4 {
    background-color: white;
    padding: 1rem;
    margin: 1rem auto;
    border-radius: 8px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
    max-width: 600px;
    text-align: left;
  }